
@import "../../css/github-gist.css";
.markdown-body{min-width:540px;max-width:800px;margin:0 auto
}
.yun-md{font-family:-apple-system,Microsoft YaHei,Arial,Microsoft JhengHei,sans-serif;line-height:1.7;color:#5e5e5e;font-size:15px
}
.yun-md h1,.yun-md h2,.yun-md h3,.yun-md h4,.yun-md h5,.yun-md h6{border:none;font-weight:normal
}
.yun-md h1{font-size:28px
}
.yun-md h2{font-size:22px;border-bottom:1px solid #f7f7f7
}
.yun-md h3{margin-top:50px;font-size:20px
}
.yun-md h4{font-size:16px
}
.yun-md h5{font-size:14px
}
.yun-md a{color:#3cbaff
}
.yun-md pre{font-size:13.6px
}
.yun-md code{background-color:#f0f6fb
}
.yun-md table tr{height:40px;border-top:none;border-bottom:1px solid #f0f6fb
}
.yun-md thead tr{background-color:#f0f6fb
}
.yun-md thead tr th{font-weight:normal
}
.yun-md tbody tr{font-size:13px;background-color:#ffffff !important
}
.yun-md thead th,.yun-md tbody td{border:none;min-width:100px
}
.yun-md .theme-color{color:#ffffff;text-align:center;width:200px;line-height:60px;border-radius:4px;display:block
}
.yun-md ul.color{padding-left:0;font-size:0
}
.yun-md ul.color li{width:110px;height:44px;line-height:44px;display:inline-block;margin:10px 20px 10px 0;text-align:center;border-radius:4px;font-size:12px
}
.yun-md ul.font-color li{width:140px
}
.yun-md ul.page-color li.mask-color{color:#ffffff;background:rgba(12,33,63,0.8)
}
.yun-md ul.page-color li.bg-color{background:#eaeff3
}
.yun-md ul.page-color li.border-color{background:#dce1e8
}
.yun-md ul.icon-list{padding:0
}
.yun-md ul.icon-list li{display:inline-block;vertical-align:top;width:110px;text-align:center;font-size:12px;margin-bottom:30px
}
.yun-md ul.icon-list li .iconfont{color:#777;cursor:pointer;font-size:40px
}
.yun-md ul.icon-list li .iconfont:hover{color:#3cbaff
}
.yun-md .utility-position{overflow:hidden;margin-bottom:20px
}
.yun-md .utility-position>div{border-radius:4px;text-align:center;width:120px;border:1px solid #dce1e8
}
.yun-md .utility-border .yun-border{background-color:rgba(0,0,0,0.02);padding:10px;margin-bottom:10px
}
.yun-md .utility-divided{padding:10px 0
}
.yun-md .utility-divided>span{padding:0 20px;margin-bottom:10px
}


